const ns = {
  NAME: "UU5.Bricks",
  CSS: "uu5-bricks",
  name(name) {
    return ns.NAME + "." + name;
  },
  css() {
    return Array.prototype.slice.call(arguments).map(name => (ns.CSS + "-" + name)).join(" ");
  }
};

export default ns;



